@use '../../utils';

@mixin flex-layout() {
    display: flex;
    align-items: center;
    place-content: center;
    position: relative;
}

.socials-container {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    min-height: 15vh;
    color: white;
    font-size: 1.1rem;
    
}

